
import exchangeBg from '../assets/charge-bg.png'
import depositBg from '../assets/despoit-bg.png'
import Group from '../components/User/Group'
import Cell from '../components/User/Cell'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import BpDialog from '../components/bpDialog'
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
XButton,
BpDialog
},
data () {
return {
exchangeVisible: false,
depositVisible: false,
exchangeBg: exchangeBg,
depositBg: depositBg,
toCoinValue: '',
toRMBValue: ''
}
},
beforeRouteEnter (to, from, next) {
document.title = '我的收益'
next()
},
methods: {
exchange () {
this.exchangeVisible = false
},
deposit () {
if ((this.toRMBValue % 100) !== 0) {
this.$vux.toast.text('提现金额不为100的倍数', 'bottom')
return
}
this.depositVisible = false
},
validToCoin (event) {
if (!Number.isInteger(this.toCoinValue)) {
this.toCoinValue = ''
return
}
if (this.toCoinValue > 102) {
this.toCoinValue = 102
}
},
validToRMB (event) {
if (!Number.isInteger(this.toRMBValue)) {
this.toRMBValue = ''
return
}
if (this.toRMBValue > 102) {
let n = 102
let min = parseInt(n / 100) * 100
this.toRMBValue = min
}
}
}
}



